"use client";

import React from 'react';
import './dashboard-styles.css'; // Создадим обычный CSS файл вместо styled-jsx

// Удаляем экспорт метаданных, так как он не работает в клиентских компонентах
// export const metadata = {
//   title: 'Дашборд',
//   description: 'Панель управления',
// }

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  // Используем useEffect для изменения стилей body только на клиенте
  React.useEffect(() => {
    // Сохраняем оригинальные стили
    const originalOverflow = document.body.style.overflow;
    const originalMargin = document.body.style.margin;
    const originalPadding = document.body.style.padding;
    
    // Применяем новые стили
    document.body.style.margin = '0';
    document.body.style.padding = '0';
    document.body.style.overflow = 'hidden';
    
    // Устанавливаем заголовок страницы через document.title
    document.title = 'Дашборд';
    
    // Возвращаем оригинальные стили при размонтировании
    return () => {
      document.body.style.overflow = originalOverflow;
      document.body.style.margin = originalMargin;
      document.body.style.padding = originalPadding;
    };
  }, []);

  return (
    <div className="dashboard-container">
      <aside className="sidebar">
        <nav>
          <ul>
            <li>
              <a href="/dashboard">Главная</a>
            </li>
            <li>
              <a href="/dashboard/news">Новости</a>
            </li>
            <li>
              <a href="/dashboard/settings">Настройки</a>
            </li>
            <li>
              <a href="/">Вернуться на сайт</a>
            </li>
          </ul>
        </nav>
      </aside>
      <main className="content">
        {children}
      </main>
    </div>
  );
}
